<template lang="pug">
    div(class="page")
        Header
        section(class="solutions")
            div(class="container")
                div(class="title")
                    h2 {{ $t("solutions") }} 
                        span {{ $t("our-solutions") }} 
                div(class="solutions-content")
                    div(class="row" v-for="solution in SolutionsData.solutions", :key="solution.id")
                        b-modal(v-model='modalShow') 
                            div(class="embed-responsive embed-responsive-16by9")
                                iframe(class="embed-responsive-item" :src="solution.video" title="YouTube video player")
                        div(class="col-md-7 col-lg-7")
                            div(class="text-part wow fadeInUp" data-wow-duration="1s")
                                h3 {{ solution.title }}
                            div(class="details-part" v-html="solution.body")
                            div(class="buttons-part")
                                a(:href="solution.pdf")
                                    fa(class="theicon" :icon="['far', 'file-pdf']")
                                    | Download pdf 
                                a(@click='modalShow = !modalShow') 
                                    fa(class="theicon" :icon="['fas', 'play']")
                                    | Show video
                        div(class="col-md-5 col-lg-5")
                            div(class="image-part wow fadeInUp" data-wow-duration="1s")
                                img(src="../assets/images/solutions/sol.png", alt="", class="")
                    .more 
                        router-link(to="/build-project" class="btn hvr-sweep-to-bottom demo") {{ $t("request-demo") }}
                        router-link(to="/build-project" class="btn hvr-sweep-to-bottom offer") {{ $t("request-offer") }}
        Footer
</template>

<script>
import Vue from "vue";
import { BootstrapVue } from "bootstrap-vue";
import { mapState } from "vuex";
// @ is an alias to /src
import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";
Vue.use(BootstrapVue);

export default {
    name: "SolutionsPage",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.SolutionsData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.SolutionsData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.SolutionsData.meta_keywords,
                },
            ],
        };
    },
    components: {
        Header,
        Footer,
    },
    data() {
        return {
            modalShow: false,
        };
    },
    async created() {
        await this.$store.dispatch("getSolutionsData");
    },
    computed: {
        ...mapState(["SolutionsData"]),
    },
};
</script>
